.block {
  position: relative;
  display: flex;
  flex-flow: column;
  border: 1px solid #f2f3f4;
  background-color: #f8f9f9;
  border-radius: 5px;
  padding: 7px;
  margin: 10px 0;

  &:last-of-type {
    margin-bottom: 20px;
  }

  &_selected {
    background: #ffd6e7;
  }
}

.name {
  font-weight: bold;
}

.tag {
  position: absolute;
  top: 7px;
  right: 0;
}
